h1, h2, h3 {
    font-weight: 500;
    line-height: 1.1;
    margin-top: 0;
    margin-block-start: 0;
    margin-block-end: 0;
}

p {
    margin-top: 0;
    margin-block-start: 0;
    margin-block-end: 0;
}

a, a:visited, a:active {
    text-decoration: none;
    color: var(--dark-grey-color);
}

a:hover {
    color: var(--mid-grey-color)
}